html {
  font-size: 16px;
}

.container {
  margin: auto;
  padding: 15px;
  display: block;
  width: 80%;
}

.btn {
  margin: 0;
  margin-right: 5px;
  padding: 6px 12px;
  border: none;
  border-radius: 5px;
}

.btn:hover {
  opacity: 0.8;
}

.btn:active {
  opacity: 0.5;
}

.btn-dark {
  background: #000000;
  color: #ffffff;
}

.btn-white {
  background: #ffffff;
  color: #000000;
  border: 1px solid #ccc;
}

.btn-add {
  background: #fff;
  color: #FF6B95;
  border: none;
  font-weight: bold;
}

.btn-trans {
  background: transparent;
  color: #000000;
  border: none;
}

.product-container {
  margin: auto;
  width: 100%;
  text-align: center;
  border-collapse: collapse;
}

.product-container tr {
  border: 1px solid #ccc;
  height: 50px;
}

.product-container thead tr:nth-child(1) {
  text-align: right;
  border: none;
}

.product-container button {
  margin: 5px;
}

.product-container tr td:nth-child(1) {
  width: 5%;
  border-right: 1px solid #ccc;
}

.product-container tr td:nth-child(2) {
  width: 30%;
}

.product-container tr td:nth-child(3) {
  width: 45%;
}

.product-container tr td:nth-child(4) {
  width: 20%;
}

.add-container {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 255;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.9);
}

.add-item {
  padding: 30px 0 50px 0;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  max-width: 400px;
  background-image: linear-gradient(-45deg, #FFC796 0%, #FF6B95 100%);
  border: none;
  border-radius: 15px;
  box-shadow: 0px 0px 50px #FF6B95;
  text-align: center;
  color: #F0EEED;
}

.add-item h2 {
  margin: 0;
  margin-bottom: 15px;
  letter-spacing: 1px;
}

.input-box, .btn-add {
  margin: auto;
  width: 60%;
  padding: 5px;
}

.btn-add {
  margin: 15px 5px;
  width: 25%;
  font-size: 1.1rem;
  box-shadow: 0 0 15px #FF6B95;
}

.input-box label {
  margin-bottom: 10px;
  padding-left: 5px;
  display: block;
  text-align: left;
  width: 100%;
  font-size: 1.1rem;
  font-weight: bold;
  letter-spacing: 1px;
}

.input-box input, .input-box textarea {
  margin:auto;
  padding: 6px 12px;
  display: block;
  width: 90%;
  border: none;
  border-radius: 5px;
  height: 30px;
  background: #F0EEED;
  color: #000;
  font-size: 1.1rem;
  font-weight: bold;
}

.input-box textarea {
  height: 10rem;
  resize: none;
  line-height: 1.5rem;
}

.input-box input:focus {
  outline: none;
  box-shadow: 0 0 15px #F0EEED;
}